// font
@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
$poppins: "Poppins";
// Here you can add other styles
html, body {
  max-width: 100%;
  overflow-x: hidden;
}

body {
  background-color: #fff;
  font-family: "Poppins" !important;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

.font-6xl{
  font-size: 3rem !important;
}

.font-7xl{
  font-size: 3.5rem !important;
}

.font-8xl{
  font-size: 4rem !important;
}

.font-9xl{
  font-size: 4.5rem !important;
}

.font-10xl{
  font-size: 5rem !important;
}

.btn-xl {
  padding: 0.5rem 1rem;
  font-size: 1.3rem;
  line-height: 1.5;
  border-radius: 0.3rem;
}

.navbar-logo {
  width: 120px;
}

.login-form {
  border: 0;
  padding: 20px;
  border-radius: 20px;

  form {
    width: 100%;

    .login-submit {
      width: 100%;
      padding: 10px 0;
      color: #fff;
      background-color: #372974;
      margin-top: 20px;
      @include media-breakpoint-down(sm){
        margin-top: 15px;
      }
    }

    .input-form {
      .form-group{
        .form-control:focus {
          box-shadow: 0 0 0 0.1rem rgb(55, 41, 116);
          border: 1px solid $pinion-primary;
        }
      }
    }

    .forgot-password {
      display: flex;
      justify-content: flex-end;

      .link,
      link:hover {
        color: #2b3c5e;
        text-decoration: none;
      }
    }

    input {
      border: 1px solid #BDBDBD;
      border-radius: 8px;
      background-color: white;
      padding-left: 35px;
    }

    input::placeholder {
      color: #BDBDBD;
    }
  }
}

.icon-inside-left {
  position: absolute;
  left: 13px;
  top: 12px;
  @include media-breakpoint-down(sm){
    top: 12px;
  }
}

.icon-see-password {
  position: absolute;
  right: 14px;
  top: 10px;
  font-size: 14px;
  @include media-breakpoint-down(sm){
    top: 11px;
  }
}

.icon-see-password:hover {
  cursor: pointer;
}

.register-form {
  .register-nav {
    margin-bottom: 15px;
    text-align: center;
    .dot {
      color: black;
      cursor: pointer;
      font-size: 36px;
      line-height: 1;
      margin: 0 15px;
      opacity: 0.4;
      text-shadow: none;
      transition: opacity 1s ease, text-shadow 1s ease;
      will-change: opacity, text-shadow;

      &.active {
        color: var(--blue);
        opacity: 1;
        text-shadow: 0 0px 8px;
      }
    }

    @include media-breakpoint-down(sm){
      display: none;
    }
  }
}
.round-100 {
  width: 35px;
  border-radius: 100%;
  height: 35px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border: 0.2px solid #333;
}
.app-header {
  .navbar-brand {
    position: relative;
    justify-content: unset;
  }
}

.projects-filter {
  position: fixed;
  top: 80px;
  left: 0px;

  @include media-breakpoint-up(lg){
    top: 110px;
    right: 20px;
  }

  @include media-breakpoint-up(xl){
    top: 100px;
    right: 150px;
  }
}

.react-datepicker__month .react-datepicker__month-text, .react-datepicker__month .react-datepicker__quarter-text {
  display: inline-block;
  width: 5rem !important;
  margin: 2px;
}

.verification-process-form {
  .upload-file-button {
    position: absolute;
    width: 200px;
    height: 200px;
    color: #dbe2f5;
    &.filled {
      color: transparent;
      background-color: transparent;
    }
    &:hover {
      color: #030303;
      background-color: whitesmoke;
      opacity: 0.5;
    }
  }
}

.profile-tabs-item {
  border: 0;
  margin: 0 5px;
  color: rgba(51, 51, 51, 0.781) !important;
  &:hover {
    border: 0;
    background: $primary !important;
    color: white !important;
    border-radius: 10px;
  }
  &.active {
    border: 0;
    background: $primary !important;
    color: white !important;
    border-radius: 10px;
  }
}

.activity-text {
  span, p{
    font-family: Poppins !important;
  }
}

.rounded-pill-right {
  border-top-right-radius: 50rem !important;
  border-bottom-right-radius: 50rem !important;
}

.rounded-pill-left {
  border-top-left-radius: 50rem !important;
  border-bottom-left-radius: 50rem !important;
}

.popover-file-list {
  max-width: 580px;
}

.rounded-5 {
  border-radius: 1.5rem;
}

.rsw_2Y {
  position: unset !important;
}

.rsw_2f {
  min-height: 1100px;
}

.app-header .navbar-brand {
  left: 0;
  margin-left: 0;
}

.navbar-pinion {
  padding-top: 10px;
  padding-bottom: 10px;
  line-height: 32px;
  @include media-breakpoint-down(md){
    padding-top: 10px;
    padding-bottom: 0;
  }

  a {
    text-decoration: none;
    color: #18568B;
  }

  li {
    margin-left: 3rem;
    @include media-breakpoint-down(md){
      margin-left: 1rem;
    }
  }
  /* padding-bottom: 20px; */
  // .nav-item
  .navbar-nav .nav-item {
    color: $pinion-primary !important;
    position: relative;
    margin-bottom: 5px;

    &.nav-button {
      .nav-subitem {
        position: absolute;
        display: none;
        white-space: nowrap;
        transform: translateX(-25%);
        a {
          color: $pinion-primary;
          font-weight: 400;
          &:hover {
            text-decoration: none;
            background-color: #f2f2f2;
          }
        }
      }
      &:hover .nav-subitem {
        display: block;
        top: 90%;
      }
    }

    &:hover {
      font-weight: 600;
    }

    &:not(.nav-button) {
      &:after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        background-color:$pinion-primary;
        height: 3px;
        border-radius: 3px;
        width: 0px;
        min-width: 0px;
        transition: width 200ms linear, min-width 200ms linear;
      }
      &:hover,
      &.active {
        &:after {
          width: 50%;
          min-width: 30px;
        }
      }
    }
  }
}

.create-project-timeline {
  @include media-breakpoint-down(md){
    white-space: nowrap;
    position: relative;
    overflow-x: scroll;
    overflow-y: hidden;
    -webkit-overflow-scrolling: touch;
    width: 500px;
    height: 350px;
  }
}